.menu-modules {
  position: fixed;
  top: 60px;
  width: 100%;
  background-color: rgba(21, 26, 35, 0.95);
  background: radial-gradient(
    ellipse farthest-side at center top,
    #283244 0%,
    rgba(21, 26, 35, 0.95) 100%
  );
  border: solid 1px rgba(255, 255, 255, 0.15);
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2);
  opacity: 1;
}

.menu-modules .modules-container {
  padding: 10px;
}

.profile-dropdown {
  width: 300px;
  position: absolute;
  top: 60px;
  right: 0;
  background-color: rgba(21, 26, 35);
  background: radial-gradient(
    ellipse farthest-side at right top,
    #4c003f 0%,
    rgb(0 0 0) 100%
  );
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2);
  opacity: 1;
  z-index: 10;
}

.close-dropDown {
  width: 100%;
  position: fixed;
  top: 0;
  height: 100%;
  background-color: #4c003f5c;
  z-index: 5;
}

.profile-dropdown .profile-dropdown-content .profile-dropdown-info {
  display: flex;
  align-items: center;
  border-bottom: solid 1px rgba(255, 255, 255, 0.15);
  padding: 15px 0 15px 15px;
}

.profile-dropdown
  .profile-dropdown-content
  .profile-dropdown-info
  .profile-dropdown-info-img {
  width: 40px;
  height: 40px;
  position: relative;
  overflow: hidden;
  border: solid 1px rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  margin-right: 15px;
}

.profile-dropdown
  .profile-dropdown-content
  .profile-dropdown-info
  .profile-dropdown-info-img
  img {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.profile-dropdown
  .profile-dropdown-content
  .profile-dropdown-info
  .profile-dropdown-info-name {
  font-size: 18px;
  line-height: 1.4;
  color: rgba(255, 255, 255, 0.7);
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
}

.profile-dropdown .profile-dropdown-content .profile-dropdown-logout {
  padding: 15px;
}

.profile-dropdown
  .profile-dropdown-content
  .profile-dropdown-logout
  .profile-dropdown-logout-button {
  background-color: #4c003f;
  padding: 7px 55px;
  border-radius: 5px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.7);
}

.profile-dropdown .profile-dropdown-content .navbar-loginButtonLink {
  border-bottom: solid 1px rgba(255, 255, 255, 0.15);
  padding: 20px;
}

.profile-dropdown .profile-dropdown-content .navbar-loginButtonLink a {
  margin: auto;
  display: block;
  background-color: #4c003f;
  border: solid 1px #770263;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  color: #fff;
  padding: 10px 50px;
  outline-offset: 2px;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  overflow: visible;
  max-width: 100%;
  transition: color 200ms, background-color 200ms, border-color 200ms;
}
.profile-dropdown .profile-dropdown-content .navbar-loginButtonLink a:hover {
  background-color: #770263;
}
.profile-dropdown .profile-dropdown-content .navbar-createAccountButtonLink {
  display: flex;
  padding: 0 15px;
}

.profile-dropdown
  .profile-dropdown-content
  .navbar-createAccountButtonLink
  span {
  padding-top: 7px;
}

.profile-dropdown .profile-dropdown-content .navbar-createAccountButtonLink a {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 20px;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: color 200ms, background-color 200ms, border-color 200ms;
}
.profile-dropdown
  .profile-dropdown-content
  .navbar-createAccountButtonLink
  a:hover {
  color: #770263;
}
